#artist {
  h2 {
    margin: 1rem 0 1rem 0;
  }
  #top-ten {
    margin: 3rem auto 0 auto;
    max-width: 800px;
    text-align: center;
  }
  .bio {
    font-size: 1.1rem;
    max-width: 35rem;
    padding: 1rem;
  }
  .video {
    margin-top: 1rem;
    .img {
      transition: .3s ease-in-out;
    }
    &:hover {
      .img {
        opacity: 0.7;
      }
    }
  }
  main {
    overflow-y: hidden;
  }
  #releases {
    margin: 2rem auto 0 auto;
    text-align: center;
    .release {
      display: grid;
      grid-template-columns: 100%;
      align-content: center;
      img {
        width: 100%;
        margin-bottom: 2rem;
      }
    }
    .left,
    .right {
      display: inline-grid;
      align-content: center;
      @media (max-width: 800px) {
        display: block;
      }
    }
  }
  &.tablet {
    main {
      display: grid;
      grid-template-columns: 1.4fr 2fr;
      justify-items: end;
      .bio {
        margin-top: 5rem;
      }
      .right {
        justify-self: start;
        padding: 0 3rem;
      }
    }
    // #releases {
